.hero {
  min-height: 90vh;
  background-image: url("./bg.avif");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  display: flex;
}

.left-box {
  width: 65%;
  padding: 3rem;
  background-image: linear-gradient(
    -90deg,
    rgba(255, 255, 255, 0) 0%,
    rgba(237, 244, 246, 0.8) 34.93%,
    rgba(237, 244, 246, 0.85) 52.08%,
    rgba(237, 244, 246, 0.9) 79.95%,
    #edf4f6 100%
  );
  display: flex;
  align-items: flex-start;
  justify-content: center;
  flex-direction: column;
}

.left-box h1 {
  width: 70%;
  font-size: 43px;
  font-family: "Poppins", sans-serif;
  font-weight: 700;
  line-height: 60px;
}

.left-box p {
  width: 421px;
  font-size: 17px;
  line-height: 26px;
  margin: 2rem 0;
}

.left-box button {
  margin-top: 1.5rem;
  border: none;
  width: 253px;
  height: 60px;
  background-color: #226500;
  color: #fff;
  border-radius: 5px;
  cursor: pointer;
  font-size: 22px;
  font-family: "Poppins", sans-serif;
  font-weight: 500;
  transition: all 0.15s linear;
}

.left-box button:hover {
  background-color: #2b7c02;
  transform: translateY(-4px);
}

.left-box button:active {
  transform: translateY(-2px);
  background-color: #226500;
}

@media (max-width: 1200px) {
  .container {
    max-width: 100%;
  }
}

@media (max-width: 992px) {
  .left-box h1 {
    font-size: 32px;
    line-height: 45px;
  }
  .left-box p {
    width: 350px;
    font-size: 16px;
  }
  .left-box button {
    width: 220px;
    height: 50px;
    font-size: 17px;
  }
}

@media (max-width: 767px) {
  .container {
    max-width: 100%;
    padding-right: 0;
    padding-left: 0;
  }
  .hero {
    margin: 0;
    min-height: 90vh;
  }
}

@media (max-width: 687px) {
  .left-box {
    width: 70%;
  }
  .left-box h1 {
    font-size: 25px;
    line-height: 35px;
  }
  .left-box p {
    width: 250px;
    font-size: 15px;
  }
  .left-box button {
    width: 180px;
    height: 40px;
    font-size: 17px;
  }
}

@media (max-width: 420px) {
  .left-box {
    width: 78%;
  }
  .left-box p {
    width: 200px;
    font-size: 14px;
    line-height: 20px;
  }
  .left-box h1 {
    width: 100%;
    font-size: 25px;
    line-height: 35px;
  }
}
